import React, {Component} from 'react';
import {connect} from 'react-redux';
import MulticartBillingAccountSelector from './MulticartBillingAccountSelectorComponent';
import MulticartBillingAccountContracts from './MulticartBillingAccountContractsComponent';
import {
    LabeledAutocompleteWithErrors,
    LabeledInputWithErrors,
    LabeledPostalCodeInputMobileWithErrors,
    LabeledPostalCodeInputWithErrors
} from 'eshop/modules/core/components/forms';
import {getBillingAccountComponentData} from '../../selectors';
import {
    changeBillingAccountFormField,
    getActiveContracts,
    setBillingAccountFormVisibility,
    setBillingAccounts,
    setCreateBillingAccount,
    setSelectedBillingAccount
} from '../../actions/app';
import {registerNextStepCondition, requestBillingAccountFormData} from '../../actions/data';
import {OraLoader} from '../../../../../eshop/components/OraCommonComponents';


export class MulticartBillingAccountsComponent extends Component {

    constructor(props) {
        super(props);
    }

    componentWillMount() {
        this.defaultAccountType = this.props.billingAccounts[0].type;
        this.props.setBillingAccounts(this.props.billingAccounts);
    }

    componentDidMount() {
        this.props.registerNextStepCondition('billingAccount');
        this.props.requestBillingAccountFormData();
    }

    getTitle() {
        return ({__html: this.props.descriptions.title})
    }

    getPropsForField(name) {
        return {
            name,
            value: this.props.formData[name] || "",
            errors: this.props.errors && this.props.errors[name],
            valid: this.props.validation[name],
            label: this.props.descriptions[name],
            labelType: 'floating',
            onChange: this.changeFormField(name)
        };
    }

    changeFormField(name) {
        return field => {
            this.props.changeBillingAccountFormField(name, field.value);
        };
    }

    toggleFormVisibility() {
        this.props.setBillingAccountFormVisibility(!this.props.visible);
    }

    closeForm() {
        this.props.setBillingAccountFormVisibility(false);
    }

    formValidated() {
        let data = this.props.formData;

        let requiredFieldsFilled = this.fieldFilled(data.postalCode) &&
            this.fieldFilled(data.town) &&
            (this.fieldFilled(data.streetName) || ((!data.streetName || data.streetName=='') && this.streetFieldDisabled())) &&
            this.fieldFilled(data.streetNumber);

        let errors = this.props.errors;
        let fieldsValid = this.noErrors(errors.postalCode) &&
            this.noErrors(errors.town) &&
            (this.noErrors(errors.streetName)) &&
            this.noErrors(errors.streetNumber) &&
            this.noErrors(errors.appartmentNo) &&
            this.noErrors(errors.emailAddress);
        return requiredFieldsFilled && fieldsValid;
    }

    fieldFilled(value) {
        return value !== '';
    }

    noErrors(errors) {
        return !errors || errors.length === 0;
    }

    streetFieldDisabled() {
        return (!!this.props.streetSuggestions) && (!this.props.streetSuggestions.length) && (!this.props.formData["streetName"].length);
    }

    getGroupedBillingAccounts() {
        var billingAccountsByType = {};
        this.props.billingAccounts.forEach(billingAccount => {
            this.addBillingAcccountToGroup(billingAccountsByType, billingAccount)
        });
        return billingAccountsByType;
    }

    addBillingAcccountToGroup(billingAccountsByType, billingAccount) {
        if (!billingAccountsByType.hasOwnProperty(billingAccount.type)) {
            billingAccountsByType[billingAccount.type] = [];
        }
        billingAccountsByType[billingAccount.type].push(billingAccount);
    }

    render() {
        let billingAccountsByType = this.getGroupedBillingAccounts.bind(this)();
        let loaderId = "accountContractsLoader";
        return (
            <OraLoader loading={this.props.isShowContractButtonEnabled} id={loaderId} parentId={"account-contracts-loader"}>
            <div className="l-page-row u-spacing-top-l billing-account-wrapper">
                {(this.props.billingAccounts.length > 0) &&
                <div className="opl-console billing-account">
                    <div className="l-row">
                        <div className="l-col l-col-small-12 l-col-medium-3 l-col-2  u-small-spacing u-padding-top-m">
                            <div dangerouslySetInnerHTML={this.getTitle()}/>
                        </div>
                        {Object.keys(billingAccountsByType).map(accountType=>
                                <MulticartBillingAccountSelector key={accountType}
                                                                 accountType={accountType}
                                                                 billingAccounts={billingAccountsByType[accountType]}
                                                                 descriptions={this.props.descriptions}/>
                        )}

                        {this.props.newAccountButtonVisibility && this.renderAddButton()}

                    </div>
                    {this.props.visible &&
                    <div>
                        <div className="l-row u-padding-top-l u-padding-l">
                            <div className="l-col l-col-12">
                                <h4 className="h3 u-no-margin">{this.props.descriptions.formLabel}</h4>
                                <div className="l-row">
                                    <div className="l-col l-col-12">
                                        <a href="#" className="u-right" onClick={this.closeForm.bind(this)}>
                                                   <span title="close" aria-hidden="true"
                                                         className="kss-icon-preview g-icon g-icon--only g-icon--xs-s g-icon--close"></span></a>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="l-row u-padding-l">
                            <div className="l-col l-col-6 l-col-small-12">
                                <div className="l-row">
                                    <div
                                        className="l-col l-col-small-6 l-col-medium-6 l-col-6 u-small-hide u-medium-hide u-padding-l">
                                        <LabeledPostalCodeInputWithErrors {...this.getPropsForField("postalCode")}/>
                                    </div>
                                    <div
                                        className="l-col l-col-small-6 l-col-medium-6 l-col-6 u-large-hide u-padding-l">
                                        <LabeledPostalCodeInputMobileWithErrors {...this.getPropsForField("postalCode")}/>
                                    </div>
                                    <div className="l-col l-col-small-12 l-col-medium-6 l-col-6 u-padding-l">
                                        <LabeledAutocompleteWithErrors {...this.getPropsForField("town")}
                                                                       suggestions={this.props.citySuggestions}/>
                                    </div>
                                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-l">
                                        <LabeledAutocompleteWithErrors {...this.getPropsForField("streetName")}
                                                                       suggestions={this.props.streetSuggestions}
                                                                       disabled={this.streetFieldDisabled.bind(this)()}/>
                                    </div>
                                    <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 u-padding-l">
                                        <LabeledInputWithErrors {...this.getPropsForField("streetNumber")}
                                                                valid={true}/>
                                    </div>

                                    <div className="l-col l-col-small-6 l-col-medium-6 l-col-6 u-padding-l">
                                        <LabeledInputWithErrors {...this.getPropsForField("appartmentNo")}
                                                                valid={true}/>
                                    </div>

                                    <div className="l-col l-col-small-12 l-col-medium-12 l-col-12 u-padding-l">
                                        <LabeledInputWithErrors {...this.getPropsForField("emailAddress")}/>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="l-row ">
                            <div className="l-col l-col-3">
                                <button className="opl-console-button--green opl-btn--medium"
                                        disabled={!this.formValidated.bind(this)()}
                                        onClick={this.props.setCreateBillingAccount.bind(this, this.defaultAccountType)}>
                                    {this.props.descriptions.createLabel}
                                </button>
                            </div>
                        </div>
                    </div>
                    }
                </div>}
                {this.props.accountContractsVisible &&
                    <MulticartBillingAccountContracts accountContracts={this.props.accountContracts} account={this.props.selectedMobile}/>
                }
            </div>
            </OraLoader>
        )
    }

    renderAddButton(){
        return (
            <div className="l-col l-col-small-12 l-col-medium-2 l-col-2  u-small-spacing">
                <button
                    className={'o-btn opl-btn opl-btn--medium' + (this.props.createEnabled ? ' opl-btn--green' : '')}
                    onClick={this.toggleFormVisibility.bind(this)}>
                                <span className="opl-ripple-box">
                                    <span className="opl-ripple-circle">
                                    </span>
                                </span>{this.props.descriptions.createNewLabel}
                </button>
            </div>)
    }
}

export default connect(getBillingAccountComponentData, {
    registerNextStepCondition,
    setBillingAccountFormVisibility,
    changeBillingAccountFormField,
    setCreateBillingAccount,
    setSelectedBillingAccount,
    requestBillingAccountFormData,
    setBillingAccounts,
    getActiveContracts
})(MulticartBillingAccountsComponent);

